export default {
	data() {
		return {
			username: "",
			password: ""
		}
	},
	methods: {
		loginSubmit() {
			this.toCheck(this.username, this.password)
			if (this.toCheck(this.username, this.password)) {
				// 将要发起请求 
				let obj = {
					email: this.username,
					password: this.password
				}
				uni.request({
					url: 'http://47.96.156.131:5000/keep/users/login',
					method: 'POST',
					header: {
						'content-type' : 'application/json'
					},
					data: {
						email: this.username,
						password: this.password
					},
					success: (res) => {
						if (res.data.callbackPasspord) {
							uni.showToast({
								icon: 'none',
								title: res.data.password
							})
						}
						if (res.data.callbackNoUser) {
							uni.showToast({
								icon: 'none',
								title: res.data.message
							})
						}
						if (res.data.success) {
							//  把 token 存到本地
							const token = res.data.token
							const tiatok = {
								Token : token,
								Time : Date.now()/1000
							}
							uni.setStorageSync('tiatok',JSON.stringify(tiatok))
							uni.switchTab({
								url: './user'
							})
						}
					},
					fail: () => {
					    console.log("失败")	
					},
					complete: () => {
					   console.log("完成")	
					}
				})
			}
		},
		loginToRegister() {
			uni.navigateTo({
				url: './register'
			})
		},
		toCheck(username, password) {
			if (!this.$validate.isEmail(this.username)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的邮箱格式'
				})
				return 0
			} else if (!this.$validate.isPwd(this.password)) {
				uni.showToast({
					icon: 'none',
					title: '请输入6位的密码'
				})
				return 0
			}
			return 1
		}
	}
}
